import { useState } from 'react'
import 'react-leaf-polls/dist/index.css'
import './main.css'
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormHelperText from '@mui/material/FormHelperText';
import FormLabel from '@mui/material/FormLabel';
import Button from '@mui/material/Button';


// Persistent data array (typically fetched from the server)


// Object keys may vary on the poll type (see the 'Theme options' table below)
const customTheme = {
    textColor: 'black',
    mainColor: '#00B87B',
    backgroundColor: 'rgb(255,255,255)',
    alignment: 'center'
}


const MainPage = () => {
    var [id, setid] = useState("")
    var [data, setData] = useState(null)
    var [isid, setisid] = useState(false)
    var [resData, setresData] = useState([])

    const [value, setValue] = useState('');
    const [error, setError] = useState(false);
    const [helperText, setHelperText] = useState('Choose wisely');

    const handleRadioChange = (event) => {
        var dadada = localStorage
        setValue(event.target.value);
        console.log(event.target.value)
        fetch(`https://VOTE-APP-BACKEND.shibamnaskar2.repl.co/data/vote?id=${id}&vid=${event.target.value}&em=${localStorage.email}`)
        .then((res)=>res.json())
        .then((res2)=>{
            console.log(res2)
            alert(res2.data)
        })
    };

    const handleSubmit = (event) => {
        
    };




    // useEffect(() => {
    //     document.getElementById("poll").style.display = "none"
    // })

    function getvoteid() {
        var dadada = localStorage
        console.log(dadada)
        console.log(id)

        fetch(`https://VOTE-APP-BACKEND.shibamnaskar2.repl.co/data/getvotebyid?id=${id}`)
            .then((res) => res.json())
            .then((res2) => {
                console.log(res2)
                if (res2.success) {
                    setData(res2.data)
                    var ddd = []
                    res2.data.nominees.map((item, index) => {
                        console.log(item)
                        var ss = {
                            id: item.id,
                            text: item.name,
                            votes: 0
                        }
                        console.log(ss)
                        ddd.push(ss)
                    })
                    setresData(ddd)
                    setisid(true)
                } else {
                    alert("enter valid vote id")
                }

            })
    }
    const handleChange = (event) => {
        setid(event.target.value);
    };
    const logout = () => {
        localStorage.clear()
        window.location.reload()
    }
    return (
        <div>
            <button onClick={logout}>Logout</button>
            <div className='mainback'>
                {isid ? (
                    <div className='pollcard' id='poll'>
                        <form onSubmit={handleSubmit}>
                            <FormControl sx={{ m: 3 }} error={error} variant="standard">
                                <FormLabel id="demo-error-radios">{data.title}</FormLabel>
                                <RadioGroup
                                    aria-labelledby="demo-error-radios"
                                    name="quiz"
                                    value={value}
                                    onChange={handleRadioChange}
                                >
                                    {
                                        resData.map((item,index)=>{
                                            return(
                                                <FormControlLabel value={item.id} control={<Radio />} label={item.text} />
                                            )
                                        })
                                    }
                                </RadioGroup>
                                <FormHelperText>{helperText}</FormHelperText>
                            </FormControl>
                        </form>
                    </div>
                ) : (
                    <div className='inputbox' id='inputid'>
                        <h1>Enter Vote Id</h1>
                        <input type="text" name="text" class="input" placeholder="Vote Id" onChange={handleChange}></input>
                        <button onClick={getvoteid}> Vote
                        </button>
                    </div>
                )}
            </div>
        </div>
    )
}

export default MainPage